container {
  width: 100%;
  margin: 0 auto;
}
.container::after, .row:after {
  content: "";
  display: table;
  clear: both;
}
[class*='col-'] {
  float: left;
  padding: 1em;
  width: 100%;
  min-height: 1px;
  display: inline-block;
}
@media only screen and (min-width: 0px) {
  .container {
    width: 95%;
    max-width: 1200px;
  }
  .col-sm-1 {
    width: 8.33333%;
  }
  .col-sm-2 {
    width: 16.66667%;
  }
